import { PureComponent, ReactElement, Fragment } from 'react';
import copy from 'copy-to-clipboard';
import logoBep20 from '../../../asset/logo/bep20.png';
import style from './CoinTitle.module.scss';

interface IStoreProps {}
interface IHOCProps {}
interface IOwnProps {
  coinTitle: string;
  address: string;
  logoURI?: string;
  web?: string;
}
interface IOwnState {
  isCopied: boolean;
}

class CoinTitle extends PureComponent<
  IOwnProps & IHOCProps & IStoreProps,
  IOwnState
> {
  state: IOwnState;

  constructor(props: IOwnProps & IHOCProps & IStoreProps) {
    super(props);

    this.state = {
      isCopied: false,
    };

    this.onCopyClipboard = this.onCopyClipboard.bind(this);
    this.onOpenLinkInfo = this.onOpenLinkInfo.bind(this);
  }

  private onCopyClipboard(address: string) {
    copy(address);

    this.setState({ isCopied: true });

    setTimeout(() => {
      this.setState({ isCopied: false });
    }, 2000);
  }

  private onOpenLinkInfo() {
    const { address, web } = this.props;

    window.open(`https://tokensniffer.com/token/${address}`);
    web && window.open(web);
  }

  render(): ReactElement {
    const { coinTitle, address, logoURI } = this.props;
    const { isCopied } = this.state;

    return (
      <Fragment>
        <button
          className={style.button}
          title='Check info!'
          onClick={this.onOpenLinkInfo}
        >
          <img
            className={style.logo}
            alt={coinTitle}
            src={logoURI || logoBep20}
            title={address}
          />
        </button>

        <a
          className={style.title}
          href={`https://poocoin.app/tokens/${address}`}
          target='_blank'
          rel='noopener noreferrer'
          title={'Link to poocoin'}
        >
          {coinTitle}
        </a>
        {isCopied ? (
          <button className={`${style.button} ${style.check}`} title='Copied!'>
            <i className='bi bi-check2'></i>
          </button>
        ) : (
          <button
            className={style.button}
            title='Copy contract to clipboard'
            onClick={() => this.onCopyClipboard(address)}
          >
            <i className='bi bi-clipboard'></i>
          </button>
        )}
        <a
          href={`${window.location.href}#/swap/${address}`}
          target='_blank'
          rel='noopener noreferrer'
          title={'Flast Swap!'}
        >
          <button className={style.button}>
            <i className='bi bi-currency-exchange'></i>
          </button>
        </a>
      </Fragment>
    );
  }
}

export default CoinTitle;
